import { css } from 'lit-element';

export default css`@charset "UTF-8";
:host {
  display: block;
  box-sizing: border-box;
  padding: 0 2em 0 1em;
  font-size: 1rem;
  line-height: 1.5rem;
  position: relative;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  cursor: pointer;
  outline: none; }

:host([hidden]), [hidden] {
  display: none !important; }

*, *:before, *:after {
  box-sizing: inherit; }

:host([tabindex='0']) {
  background: #e9e9e9; }

:host([aria-selected=true])::after {
  content: '✔';
  position: absolute;
  right: 0.5em; }

:host([aria-disabled=true]) {
  opacity: .5;
  cursor: default; }
`;